import axios from 'axios'
import io from "socket.io-client";

const API_URI = 'https://itatsuya1250-chat.glitch.me/chat'
const isSocket = io("https://itatsuya1250-chat.glitch.me", { path: '/chat', autoConnect: false });


export default {
	state: {
		user: null,
		ioSocket: isSocket,
		chatData: [],
		isInit: false,
	},
	getters: {
		user: state => {
			return state.user
		},
		chatData: state => {
			return state.chatData
		},
	},
	actions: {
		post({ commit }, prams = null) {
			let data = {}
			if (prams) data = prams
			axios.post(API_URI, data)
				.then(res => {
					if (res.status === 200 || res.status === 304) {
						if (res.data.error) {
							commit('failure', res.data)
						} else {
							commit('success', { data: res.data, callback: data.callback })
						}
					}
				})
				.catch(error => {
					throw error
				})
		},
	},
	mutations: {
		filure() {
		},
		success(state, data) {
			state.user = data.data.user
			if (data.callback) {
				data.callback(data.data)
			}
		},
		open(state) {
			state.ioSocket.open()
			if (state.isInit === false) {
				state.ioSocket.on('msg', data => {
					state.chatData = [...state.chatData, data];
				});
				state.isInit = true
			}
		},
		close(state) {
			state.ioSocket.close()
		},
		msg(state, msg) {
			let data = { msg: msg, user: state.user, date: null, read: false };
			state.ioSocket.emit('msg', data);
		},
	}
}